/**
 * Updates a benefit item.
 *
 * Each time the benefit item is updated,
 * `revision` increments by 1.
 * The current `revision` must be passed when updating the benefit item.
 * This ensures you're working with the latest benefit item
 * and prevents unintended overwrites.
 */
export function updateItem(payload: object): RequestOptionsFactory<any> {
  function __updateItem({ host }: any) {
    const serializedData = transformPaths(payload, [
      {
        transformFn: transformSDKFieldMaskToRESTFieldMask,
        paths: [{ path: 'fieldMask' }],
      },
      {
        transformFn: transformSDKTimestampToRESTTimestamp,
        paths: [{ path: 'item.createdDate' }, { path: 'item.updatedDate' }],
      },
    ]);
    const metadata = {
      entityFqdn: 'wix.benefit_programs.v1.item',
      method: 'PATCH' as any,
      methodFqn: 'wix.benefit_programs.v1.item.ItemService.UpdateItem',
      packageName: PACKAGE_NAME,
      url: resolveWixBenefitProgramsV1ItemItemServiceUrl({
        protoPath: '/v1/items/{item.id}',
        data: serializedData,
        host,
      }),
      data: serializedData,
      transformResponse: (payload: any) =>
        transformPaths(payload, [
          {
            transformFn: transformRESTTimestampToSDKTimestamp,
            paths: [{ path: 'item.createdDate' }, { path: 'item.updatedDate' }],
          },
        ]),
    };

    return metadata;
  }

  return __updateItem;
}

