export function createItem(httpClient: HttpClient): CreateItemSignature {
  return (
    item: NonNullablePaths<
      Item,
      `externalId` | `itemSetId` | `namespace` | `providerAppId`,
      2
    >
  ) =>
    universalCreateItem(
      item,
      // @ts-ignore
      { httpClient }
    );
}

interface CreateItemSignature {
  /**
   * Creates a benefit item.
   * @param - Item to create.
   * @returns Created item.
   */
  (
    item: NonNullablePaths<
      Item,
      `externalId` | `itemSetId` | `namespace` | `providerAppId`,
      2
    >
  ): Promise<Item>;
}

export function bulkCreateItems(
  httpClient: HttpClient
): BulkCreateItemsSignature {
  return (
    items: NonNullablePaths<
      Item,
      `externalId` | `itemSetId` | `namespace` | `providerAppId`,
      2
    >[],
    options?: BulkCreateItemsOptions
  ) =>
    universalBulkCreateItems(
      items,
      options,
      // @ts-ignore
      { httpClient }
    );
}

interface BulkCreateItemsSignature {
  /**
   * Creates benefit items.
   * @param - Items to create.
   */
  (
    items: NonNullablePaths<
      Item,
      `externalId` | `itemSetId` | `namespace` | `providerAppId`,
      2
    >[],
    options?: BulkCreateItemsOptions
  ): Promise<
    NonNullablePaths<
      BulkCreateItemsResponse,
      | `results`
      | `results.${number}.itemMetadata.originalIndex`
      | `results.${number}.itemMetadata.success`
      | `results.${number}.itemMetadata.error.code`
      | `results.${number}.itemMetadata.error.description`
      | `bulkActionMetadata.totalSuccesses`
      | `bulkActionMetadata.totalFailures`
      | `bulkActionMetadata.undetailedFailures`,
      6
    >
  >;
}

