export function updateItem(httpClient: HttpClient): UpdateItemSignature {
  return (_id: string, item: NonNullablePaths<UpdateItem, `revision`, 2>) =>
    universalUpdateItem(
      _id,
      item,
      // @ts-ignore
      { httpClient }
    );
}

interface UpdateItemSignature {
  /**
   * Updates a benefit item.
   *
   * Each time the benefit item is updated,
   * `revision` increments by 1.
   * The current `revision` must be passed when updating the benefit item.
   * This ensures you're working with the latest benefit item
   * and prevents unintended overwrites.
   * @param - Benefit item ID.
   * @returns Updated item.
   */
  (
    _id: string,
    item: NonNullablePaths<UpdateItem, `revision`, 2>
  ): Promise<Item>;
}

